@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap");
$navigation_font_size: calc(1.25rem + 0.1vw);
$logo_font_size: calc(1.5rem + 1.4vw);
$logo_font_family: "Dancing Script";

.navigation {
    display: flex;
    flex-flow: row wrap;
    height: fit-content;
    padding: 6px;
    background: rgb(63, 94, 251);
    background: linear-gradient(
        90deg,
        rgba(63, 94, 251, 1) 0%,
        rgba(252, 70, 107, 1) 100%
    );
    .navigation_logo_container {
        display: flex;
        flex-grow: 1;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        span {
            margin-left: 10px;
            color: white;
            font-size: $logo_font_size;
            font-family: $logo_font_family;
        }
    }
    .navigation_buttons_container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        flex-grow: 1;
        > div {
            height: 80%;
            padding-left: 12px;
            padding-right: 12px;
            padding-top: 6px;
            padding-bottom: 6px;
            transition: background-color 0.3s;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            span {
                color: white;
                font-size: $navigation_font_size;
            }
        }
        > div:hover {
            background-color: hsla(0, 0%, 0%, 40%);
        }
    }
    .navigation_logo {
        max-width: 75px;
    }
}
.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.hamRotate.active {
    transform: rotate(45deg);
}
.hamRotate180.active {
    transform: rotate(180deg);
}
.line {
    fill: none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke: white;
    stroke-width: 5.5;
    stroke-linecap: round;
}
.ham1 .top {
    stroke-dasharray: 40 139;
}
.ham1 .bottom {
    stroke-dasharray: 40 180;
}
.ham1.active .top {
    stroke-dashoffset: -98px;
}
.ham1.active .bottom {
    stroke-dashoffset: -138px;
}
.mobile_nav {
    display: none;
}

.react-slidedown {
    height: 0;
    overflow: hidden;
    transition-property: none;
    transition-duration: 0.5s;
    transition-timing-function: ease-in-out;
}

@media screen and (max-width: 750px) {
    .navigation {
        flex-flow: column nowrap;
    }
    .mobile_nav {
        display: block;
    }
    .desktop_nav{
        display: none !important;
    }
    .navigation_logo{
        max-width: 65px !important;
    }
    .navigation_buttons_container {
        margin-top: 10px !important;
        justify-content: center !important;
        flex-direction: column !important;
        > div {
            width: 100% !important;
            padding: 14px !important;
            align-items: flex-start !important;
            height: auto !important;
        }
    }
}
